.line-high-grp {
  .grp-heading {
    color: '#000000';
    font-family: 'MuliBlack';
    font-size: 18px;
  }
}

.PC-dashboard {
  .line-charts-box {
    min-height: 534px;
    margin-top: 10px;
    border-radius: 6px;
    background-color: #FFFFFF;
    box-shadow: 0 6px 24px 0 rgba(0, 0, 0, 0.2);
    margin-bottom: 40px;
    width: 100%;
    padding: 20px;
  }

  .legend-lable {
    color: #030f09;
    font-family: "MuliBold";
    font-size: 16px;
    line-height: 20px;
  }

  .legend-counts .amount {
    color: #fa7083;
    font-family: "MuliBold";
    font-size: 36px;
    text-shadow: 0 0 10px 0 rgba(0, 0, 0, .1);
  }
}
